import EditForm from './forms/EditForm';
import LikeButton from './buttons/LikeButton';
import MoreButton from './buttons/MoreButton';
import Replies from './Replies';
import {Avatar, BlankAvatar} from './Avatar';
import {Comment, useAppContext} from '../../AppContext';
import {Transition} from '@headlessui/react';
import {isCommentPublished} from '../../utils/helpers';
import {useState} from 'react';

type AnimatedCommentProps = {
    comment: Comment;
    parent?: Comment;
};

const AnimatedComment: React.FC<AnimatedCommentProps> = ({comment, parent}) => {
    return (
        <Transition
            enter="transition-opacity duration-300 ease-out"
            enterFrom="opacity-0"
            enterTo="opacity-100"
            leave="transition-opacity duration-100"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
            show={true}
            appear
        >
            <EditableComment comment={comment} parent={parent} />
        </Transition>
    );
};

type EditableCommentProps = AnimatedCommentProps;
const EditableComment: React.FC<EditableCommentProps> = ({comment, parent}) => {
    const [isInEditMode, setIsInEditMode] = useState(false);

    const closeEditMode = () => {
        setIsInEditMode(false);
    };

    const openEditMode = () => {
        setIsInEditMode(true);
    };

    if (isInEditMode) {
        return (
            <EditForm close={closeEditMode} comment={comment} parent={parent} />
        );
    } else {
        return (<CommentComponent comment={comment} openEditMode={openEditMode} parent={parent} />);
    }
};

type CommentProps = AnimatedCommentProps & {
    openEditMode: () => void;
};
const CommentComponent: React.FC<CommentProps> = ({comment, parent, openEditMode}) => {
    const isPublished = isCommentPublished(comment);

    if (isPublished) {
        return (<PublishedComment comment={comment} openEditMode={openEditMode} parent={parent} />);
    }
    return (<UnpublishedComment comment={comment} openEditMode={openEditMode} />);
};

const PublishedComment: React.FC<CommentProps> = ({comment}) => {
    return (
        <CommentLayout
            actions={<CommentMenu comment={comment} />}
            avatar={<Avatar comment={comment} />}
            body={<CommentBody html={comment.html} />}
            header={<CommentHeader comment={comment} />}
        />
    );
};

type UnpublishedCommentProps = {
    comment: Comment;
    openEditMode: () => void;
};

const UnpublishedComment: React.FC<UnpublishedCommentProps> = ({comment}) => {
    const {admin, t} = useAppContext();

    let notPublishedMessage;
    if (admin && comment.status === 'hidden') {
        notPublishedMessage = t('このコメントは非表示です');
    } else {
        notPublishedMessage = t('このコメントは削除されました');
    }

    const avatar = (<BlankAvatar />);

    const header = (
        <div className="flex items-center justify-between">
            <p className="mt-[4px] font-sans text-[16px] italic leading-normal text-[rgba(0,0,0,0.2)] dark:text-[rgba(255,255,255,0.35)]">
                {notPublishedMessage}
            </p>
            <div className="mt-[4px]">
                <MoreButton comment={comment} />
            </div>
        </div>
    );

    const body = (
        <RepliesContainer comment={comment} />
    );

    return (
        <CommentLayout avatar={avatar} body={body} header={header} />
    );
};

// Helper components

const MemberExpertise: React.FC<{comment: Comment}> = ({comment}) => {
    const {member} = useAppContext();
    const memberExpertise = member && comment.member && comment.member.uuid === member.uuid ? member.expertise : comment?.member?.expertise;

    if (!memberExpertise) {
        return null;
    }

    return (
        <span className="[overflow-wrap:anywhere]">{memberExpertise}</span>
    );
};

const EditedInfo: React.FC<{comment: Comment}> = ({comment}) => {
    const {t} = useAppContext();
    if (!comment.edited_at) {
        return null;
    }
    return (
        <span>
            <span className="mx-[0.3em]">·</span>{t('Edited')}
        </span>
    );
};

const RepliesContainer: React.FC<{comment: Comment}> = ({comment}) => {
    const hasReplies = comment.replies && comment.replies.length > 0;

    if (!hasReplies) {
        return null;
    }

    return (
        <div className="mb-4 mt-10 sm:mb-0">
            <Replies comment={comment} />
        </div>
    );
};

//
// -- Published comment components --
//

// TODO: move name detection to helper
const AuthorName: React.FC<{comment: Comment}> = ({comment}) => {
    const {t} = useAppContext();
    const name = !comment.member ? t('Deleted member') : (comment.member.name ? comment.member.name : t('Anonymous'));
    return (
        <h4 className="font-sans text-sm font-normal tracking-normal text-neutral-800">
            {name}
        </h4>
    );
};

const CommentHeader: React.FC<{comment: Comment}> = ({comment}) => {
    return (
        <div className="flex flex-col">
            <AuthorName comment={comment} />
            <div className="flex items-baseline font-sans text-[12px] tracking-tight text-neutral-800">
                <MemberExpertise comment={comment}/>
                <EditedInfo comment={comment} />
            </div>
        </div>
    );
};

type CommentMenuProps = {
    comment: Comment;
};
const CommentMenu: React.FC<CommentMenuProps> = ({comment}) => {
    return (
        <div className="flex items-center gap-5">
            {<LikeButton comment={comment} />}
            {<MoreButton comment={comment} />}
        </div>
    );
};

const CommentBody: React.FC<{html: string}> = ({html}) => {
    const dangerouslySetInnerHTML = {__html: html};
    return (
        <div className="mt mb-1 flex flex-row items-center gap-4">
            <p dangerouslySetInnerHTML={dangerouslySetInnerHTML} className="gh-comment-content break-all font-sans text-[14px] font-normal leading-normal tracking-widest text-neutral-600" data-testid="comment-content"/>
        </div>
    );
};

//
// -- Layout --
//

type CommentLayoutProps = {
    avatar: React.ReactNode;
    header: React.ReactNode;
    body: React.ReactNode;
    actions?: React.ReactNode;
};

const CommentLayout: React.FC<CommentLayoutProps> = ({avatar, header, body, actions}) => {
    return (
        <div className="mb-4 flex w-full flex-col gap-2 rounded-[8px] bg-white p-[12px] " data-testid="comment-component">
            <div className="flex items-center gap-2">
                <div className="mr-3 flex">
                    {avatar}
                </div>
                <div className="grow">
                    {header}
                </div>
                <div>
                    {actions}
                </div>
            </div>
            <div className="mt-1 pl-2">
                {body}
            </div>
        </div>
    );
};

//
// -- Default --
//

export default AnimatedComment;
