import {Comment, useAppContext} from '../../../AppContext';
import {LikeAvatar} from '../LikeAvatar';
import {ReactComponent as LikeIcon} from '../../../images/icons/like.svg';
import {useState} from 'react';

type Props = {
    comment: Comment;
};
const LikeButton: React.FC<Props> = ({comment}) => {
    const {dispatchAction, member, commentsEnabled} = useAppContext();
    // const [animationClass, setAnimation] = useState('');
    const [localComment, setLocalComment] = useState<Comment>(comment);
    const [showLikes, setShowLikes] = useState(false);
    const [disabled, setDisabled] = useState(false);

    const paidOnly = commentsEnabled === 'paid';
    const isPaidMember = member && !!member.paid;
    const canLike = member && (isPaidMember || !paidOnly);

    const toggleLike = () => {
        if (!canLike || !member) {
            dispatchAction('openPopup', {
                type: 'ctaPopup'
            });
            return;
        }

        if (!localComment.liked) {
            setDisabled(true);
            setLocalComment((prev) => {
                const updated = {...prev};

                updated.liked = true;
                updated.count = {...prev.count, likes: prev.count.likes + 1};

                const prevLikesMember = Array.isArray(prev.likes_member) ? prev.likes_member : [];

                const alreadyExists = prevLikesMember.some(m => m.uuid === member.uuid);
                if (!alreadyExists) {
                    updated.likes_member = [...prevLikesMember, member];
                } else {
                    updated.likes_member = prevLikesMember;
                }

                return updated;
            });

            dispatchAction('likeComment', localComment);
            setDisabled(false);
            // setAnimation('animate-heartbeat');
            // setTimeout(() => {
            //     setAnimation('');
            // }, 400);
        } else {
            setDisabled(true);
            setLocalComment((prev) => {
                const updated = {...prev};

                updated.liked = false;
                updated.count = {...prev.count, likes: Math.max(prev.count.likes - 1, 0)};

                const prevLikesMember = Array.isArray(prev.likes_member) ? prev.likes_member : [];

                updated.likes_member = prevLikesMember.filter(m => m.uuid !== member.uuid);

                return updated;
            });

            dispatchAction('unlikeComment', localComment);
            setDisabled(false);
        }
    };

    // If can like: use <button> element, otherwise use a <span>
    let likeCursor = 'cursor-pointer';
    if (!canLike) {
        likeCursor = 'cursor-text';
    }

    return (
        <div
            className="relative inline-block"
            onMouseEnter={() => setShowLikes(true)}
            onMouseLeave={() => setShowLikes(false)}
            onTouchEnd={() => setShowLikes(false)}
            onTouchStart={() => setShowLikes(true)}
        >
            <button
                className={`group flex items-center justify-center gap-[6px] font-sans text-sm outline-0
                    ${localComment.liked ? 'text-red-500' : 'text-[rgba(0,0,0,0.5)] dark:text-[rgba(255,255,255,0.5)]'}
                    ${!localComment.liked && canLike && 'hover:text-[rgba(0,0,0,0.75)] hover:dark:text-[rgba(255,255,255,0.25)]'}
                    ${likeCursor}`}
                data-testid="like-button"
                disabled={disabled}
                type="button"
                onClick={toggleLike}
            >
                <LikeIcon
                    className={`${
                        localComment.liked
                            ? 'fill-red-500 stroke-red-500'
                            : 'stroke-[rgba(0,0,0,0.5)] dark:stroke-[rgba(255,255,255,0.5)]'
                    }
                    ${
        !localComment.liked && canLike &&
                        'group-hover:stroke-[rgba(0,0,0,0.75)] dark:group-hover:stroke-[rgba(255,255,255,0.25)]'
        }`}
                />
                <span className="min-w-[10px] text-center">{localComment.count.likes}</span>
            </button>

            {showLikes && localComment.likes_member && localComment.likes_member.length > 0 && (
                <div
                    className="absolute bottom-6 right-0 z-10 mt-2 max-h-64 w-52 overflow-y-auto rounded border border-gray-200 bg-white px-3 py-2 text-black shadow-sm dark:border-gray-600 dark:bg-gray-800 dark:text-white"
                >
                    {localComment.likes_member.map(m => (
                        <div key={m.id} className="mb-1 flex items-center justify-between gap-2 last:mb-0">
                            <div className="flex items-center gap-2">
                                <LikeAvatar member={m} />
                                <span className="text-[10px]">{m.name}</span>
                            </div>
                            <LikeIcon className="ml-auto fill-red-500 stroke-red-500" />
                        </div>
                    ))}
                </div>
            )}
        </div>
    );
};

export default LikeButton;
